import React, { useState, useEffect } from "react";
import ServiceWrapper from "../components/ServiceWrapper";
import SongRow from "../components/SongRow";
import {
  Container,
  SectionTitle,
  TopWrap,
  ContentsWrap,
  TopFilterButton,
  TopFilterButtonIconWrap,
} from "../styles/pages/Playlist.style";
import styled from "@emotion/styled";
import Button from "../components/Button";
import Modal from "../components/Modal";
import Input from "../components/Input";
import NotFoundImage from "../assets/search-not-found.svg";
import SubmitButtonIcon from "../assets/request-song-icon.svg";
import InputWithLabel from "../components/InputWithLabel";
import TopFilterButtonIcon from "../assets/top-filter-playlist-icon.svg";
import TopFilterCheckIcon from "../assets/filter-top-button-icon.svg";
import { api } from "../utils/api";
import auth from "../utils/auth";
import { PlaylistRow, PlaylistWrap } from "../components/PlaylistModal";
import { IPlaylist, ISearchData, ISong } from "../types";
import { makeNewPlaylist, getPlaylist, savePlaylist } from "../utils/playlist";
import { useHistory, useParams } from "react-router";
import useWindowSize from "../utils/useWindowSize";
import MobileSearchHeader from "../components/MobileSearchHeader";

const Search = () => {
  const history = useHistory();
  const params = useParams<{ searchParams: string }>();
  const [isChecked, setIsChecked] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [isOpenPlaylistModal, setIsOpenPlaylistModal] = useState(false);
  const [isNewPlaylist, setIsNewPlaylist] = useState(false);
  const [isOpenRequestModal, setIsOpenRequestModal] = useState(false);
  const [info, setInfo] = useState<{ singer: string; title: string }>({
    singer: "",
    title: "",
  });
  const [isLoading, setIsLoading] = useState(true);
  const [playlists, setPlaylists] = useState<Array<IPlaylist>>([]);
  const [newPlaylistName, setNewPlaylistName] = useState<string>("");
  const [data, setData] = useState<ISearchData>();
  const [page, setPage] = useState(1);
  const [musics, setMusics] = useState<Array<ISong>>([]);
  const [checkedSongs, setCheckedSongs] = useState<Array<number>>([]);
  const { width: screenWidth = 1980 } = useWindowSize();

  const [searchValue, setSearchValue] = useState<string>("");

  const handleChange = (e: any) => {
    e.persist();
    setInfo((prevState) => ({ ...prevState, [e.target.name]: e.target.value }));
  };

  const handleRequest = async () => {
    if (auth.getToken()) {
      await api.post("/user/score/request", {
        score_title: info.title,
        score_singer: info.singer,
      });
    } else {
      await api.post("/score/request", {
        score_title: info.title,
        score_singer: info.singer,
      });
    }
    setIsOpenRequestModal(false);
    setIsSubmitted(true);
  };

  useEffect(() => {
    const infiniteScroll = () => {
      const scrollHeight = Math.max(
        document.documentElement.scrollHeight,
        document.body.scrollHeight
      );
      const scrollTop = Math.max(
        document.documentElement.scrollTop,
        document.body.scrollTop
      );
      const clientHeight = document.documentElement.clientHeight;

      if (scrollTop + clientHeight > scrollHeight - 300) {
        setPage((prevState) => prevState + 1);
      }
    };
    window.addEventListener("scroll", infiniteScroll, true);
    return () => {
      window.removeEventListener("scroll", infiniteScroll, true);
    };
  }, []);

  // useEffect(() => {
  //   if (auth.getToken()) {
  //     getPlaylist().then((playlists) => setPlaylists(playlists));
  //   }
  // }, []);

  useEffect(() => {
    const fetchData = async () => {
      const {
        data: { data },
      } = await api.get("/search/index", {
        params: { keyword: params.searchParams, page },
      });
      setData(data);
      setMusics((prevState) => [...prevState, ...data.musics.data]);
      setIsLoading(false);
    };

    setSearchValue(params.searchParams);
    if (data?.musics.next_page_url || page === 1) {
      fetchData();
    }
    // eslint-disable-next-line
  }, [page, params.searchParams]);

  const handleCheckSongRow = (song: any) => {
    if (checkedSongs.includes(song.id)) {
      setCheckedSongs((prevState) => {
        let temp = [...prevState];
        temp.splice(temp.indexOf(song.id), 1);
        return temp;
      });
    } else {
      setCheckedSongs((prevState) => {
        let temp = [...prevState];
        temp.push(song.id);
        return temp;
      });
    }
  };

  const isSmallScreen = screenWidth <= 500;
  const count = musics.length;

  return (
    <>
      <ServiceWrapper isHeaderRendered={!isSmallScreen}>
        {isSmallScreen && (
          <MobileSearchHeader
            value={searchValue}
            onChange={(e) => setSearchValue(e.target.value)}
            onKeyPress={(e) => {
              if (e.key === "Enter") {
                history.push(`/search/${searchValue}`);
                window.location.reload();
              }
            }}
          />
        )}
        <Container>
          {count > 0 ? (
            <ContentsWrap>
              <TopWrap>
                <SectionTitle>{data?.musics?.total}건의 검색 결과</SectionTitle>
                {isSmallScreen ? (
                  <MobileButtonList>
                    <MobileButton
                      onClick={() =>
                        auth.getToken()
                          ? setIsOpenPlaylistModal(true)
                          : history.push("/playlist")
                      }
                    >
                      <TopFilterButtonIconWrap>
                        <TopFilterButtonIcon />
                      </TopFilterButtonIconWrap>
                    </MobileButton>
                    <MobileButton
                      active={isChecked}
                      onClick={() => setIsChecked((prevState) => !prevState)}
                    >
                      <TopFilterButtonIconWrap>
                        <TopFilterCheckIcon />
                      </TopFilterButtonIconWrap>
                    </MobileButton>
                  </MobileButtonList>
                ) : (
                  <TopFilterButtonsWrap>
                    {isChecked && (
                      <TopFilterButton
                        onClick={() =>
                          auth.getToken()
                            ? setIsOpenPlaylistModal(true)
                            : history.push("/playlist")
                        }
                      >
                        <TopFilterButtonIconWrap>
                          <TopFilterButtonIcon />
                        </TopFilterButtonIconWrap>
                        PLAYLIST 담기
                      </TopFilterButton>
                    )}
                    <TopFilterButton
                      active={isChecked}
                      onClick={() => setIsChecked((prevState) => !prevState)}
                    >
                      <TopFilterButtonIconWrap>
                        <TopFilterCheckIcon />
                      </TopFilterButtonIconWrap>
                      선택하기
                      {isChecked && <b>{checkedSongs.length}</b>}
                    </TopFilterButton>
                  </TopFilterButtonsWrap>
                )}
              </TopWrap>
              {musics.map((song) => (
                <SongRow
                  key={`song-row-${song.id}`}
                  id={song.id}
                  title={song.title}
                  artist={song.singer}
                  image={song.image_url}
                  visibleChecked={isChecked}
                  checked={checkedSongs.includes(song.id)}
                  onClick={() =>
                    isChecked
                      ? handleCheckSongRow(song)
                      : history.push(`/notes/${song.id}`)
                  }
                />
              ))}
            </ContentsWrap>
          ) : (
            !isLoading && (
              <NotFoundContainer>
                <NotFoundImageWrap>
                  <NotFoundImage />
                </NotFoundImageWrap>
                <NotFoundInfo>
                  검색하신 악보를 못찾았습니다.
                  <br />
                  원하시는 악보를 신청하실 수 있습니다.
                </NotFoundInfo>
                <SubmitButton
                  styleType="blue"
                  onClick={() => {
                    getPlaylist().then();
                    setIsOpenRequestModal(true);
                  }}
                >
                  <SubmitButtonIconWrap>
                    <SubmitButtonIcon />
                  </SubmitButtonIconWrap>
                  악보 신청하기
                </SubmitButton>
              </NotFoundContainer>
            )
          )}
        </Container>
      </ServiceWrapper>
      {isOpenPlaylistModal && (
        <Modal
          open={isOpenPlaylistModal}
          onClose={() => setIsOpenPlaylistModal(false)}
          title={!isNewPlaylist ? "PLAYLIST" : "NEW PLAYLIST"}
          firstButtonLabel={
            !isNewPlaylist ? "새 플레이리스트 생성" : "생성하기"
          }
          onClickFirstButton={
            isNewPlaylist
              ? async () => {
                  await makeNewPlaylist(newPlaylistName);
                  const playlists = await getPlaylist();
                  setPlaylists(playlists);
                  setIsNewPlaylist(false);
                }
              : () => setIsNewPlaylist(true)
          }
          secondButtonLabel="취소"
          onClickSecondButton={() =>
            isNewPlaylist
              ? setIsNewPlaylist(false)
              : setIsOpenPlaylistModal(false)
          }
          content={
            <>
              {!isNewPlaylist ? (
                <PlaylistWrap>
                  {playlists.map((playlist) => (
                    <PlaylistRow
                      key={`playlist-${playlist.id}`}
                      id={playlist.id}
                      title={playlist.name}
                      count={playlist.total_count}
                      onClick={async (id: number) => {
                        await savePlaylist(id, checkedSongs.toString());
                        const playlists = await getPlaylist();
                        setPlaylists(playlists);
                        setIsOpenPlaylistModal(false);
                      }}
                    />
                  ))}
                </PlaylistWrap>
              ) : (
                <StyledInput
                  icon="edit"
                  placeholder="PLAYLIST 이름을 입력해주세요"
                  value={newPlaylistName}
                  onChange={(e) => setNewPlaylistName(e.target.value)}
                />
              )}
            </>
          }
        />
      )}
      <Modal
        open={isOpenRequestModal}
        onClose={() => setIsOpenRequestModal(false)}
        firstButtonLabel="확인"
        secondButtonLabel="취소"
        onClickFirstButton={handleRequest}
        onClickSecondButton={() => setIsOpenRequestModal(false)}
        title="악보신청"
        content={
          <InputWrap>
            <InputWithLabel
              name="singer"
              label="가수"
              placeholder="가수를 입력해주세요."
              value={info.singer}
              onChange={handleChange}
            />
            <InputWithLabel
              name="title"
              label="제목"
              placeholder="제목을 입력해주세요."
              value={info.title}
              onChange={handleChange}
            />
          </InputWrap>
        }
      />
      <Modal
        open={isSubmitted}
        onClose={() => setIsSubmitted(false)}
        firstButtonLabel="확인"
        onClickFirstButton={() => setIsSubmitted(false)}
        title="악보신청"
        content={
          <>
            악보 신청이 완료되었습니다. 결과는
            <br />
            <b>[공지시항 또는 내가 신청한 악보]</b>에서
            <br />
            확인 할 수 있습니다.
          </>
        }
      />
    </>
  );
};

const TopFilterButtonsWrap = styled.div`
  display: flex;
`;

const NotFoundContainer = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 383px;
  height: 100%;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
`;

const NotFoundImageWrap = styled.div`
  margin-bottom: 40px;

  html[color-mode="dark"] & path[stroke-width="2"] {
    stroke: white !important;
  }
`;

const NotFoundInfo = styled.span`
  display: block;
  opacity: 0.8;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.38;
  text-align: center;
  color: #1e1e1e;
  margin-bottom: 40px;

  html[color-mode="dark"] & {
    color: white;
  }
`;

const SubmitButton = styled(Button)`
  max-width: 191px;
  padding: 15px 28px;
`;

const SubmitButtonIconWrap = styled.div`
  display: flex;
  margin-right: 12px;
`;

const StyledInput = styled(Input)`
  margin-top: 36px;

  html[color-mode="dark"] & {
    background-color: #2e3137;
  }
`;

const InputWrap = styled.div`
  display: flex;
  flex-direction: column;

  & > * {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }
`;

export default Search;

const MobileButtonList = styled.div`
  display: flex;
`;

const MobileButton = styled(TopFilterButton)`
  margin-left: 5px;
  width: 35px;
  min-height: 35px;
  opacity: 0.08;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
`;
